.form-cont{
    margin: 4rem 6rem;
    color: #2a2a2a;
}

.form-cont form{
    padding-top: 3rem;
    display: flex;
    flex-direction: column;
    width: 50%;
    margin: auto;

}

.form-cont input{
    height: 3rem;
    padding: 0 1rem;
    margin-bottom: 2rem;
    border-radius: .3rem;
    border: 1px solid #2a2a2a;

}

.form-cont textarea{
    padding: 1rem;
    margin-bottom: 2rem;
    border-radius: .3rem;
    border: 1px solid #2a2a2a;

}

.form-cont button{
    display: inline;
   

}
.btn5{
    margin: .8rem 0;
    padding: 1rem 0;
   width: 100%;
   font-size: 1rem;
   
}
.btn5:hover{
    background: grey;
    color: black;
    cursor: pointer;
}

@media screen and (max-width:850px) {
    .form-cont{
        margin: 4rem 2rem;
    }.form-cont form{
        padding-top: 2rem;
        width: 90%;
    }
}