#nv{
    display: flex;
    align-items: center;
    justify-content: space-between;
    background: rgb(218, 244, 240);
    padding: 0px 30px;
    box-shadow: 0 5px 15px rgb(0,0,0.06);
    width: 90%;
    height: 80px;
    border-radius: 10px;
    position: fixed;
    top: 10px;
    left: 20px;
    transform: translate(0);
    z-index: 9999;
}
#nv a{
    font-size: 2rem;
    justify-content: space-between;
    text-decoration: none;
    color: black;
    transition: 0.3s ease-in-out;
}
#nv a:hover{
    color: orangered;
    
}
#navbar{
    display: flex;
    align-items: center;
    justify-content: center;
}

#navbar li{
    list-style: none;
    padding: 0 20px;
    position: relative;
}

#navbar li a{
    text-decoration: none;
    font-size: 1.3rem;
    font-weight: 600;
    color: black;
    transition: 0.3s ease-in-out;
}
.sign{
    border: 2px solid black;
    border-radius: 10px;
}
#navbar li a:hover,
#navbar li a.active{
    color:orangered;
}

#navbar li a:hover::after,
#navbar li a.active::after{
    content: "";
    width: 30%;
    height: 2px;
    background:orangered;
    position: absolute;
    bottom: -4px;
    left: 20px;
}

#mobile{
    display: none;
}

#mobile i{
    color: black;
    align-items: center;
}

@media screen and(max-width:769px){
    #navbar{
        display: flex;
        flex-direction: column;
        align-items: flex-start;
        justify-content: flex-start;
        position: fixed;
        top: 90px;
        right: 0px;
        width: 300px;
        height: 100vh;
        background: #2a3239;
        box-shadow: 0 40px 60px rgba(0, 0,0,0.1);
        padding: 40px 0 0 10px;
    }   

    #navbar li{
        margin-bottom: 25px;
    }
    #mobile{
        display:block;
    }
    #mobile i{
        font-size: 4px;
        cursor: pointer;
    }
}