.hero{
    width: 100%;
    height: 100vh;
    position: relative;
}

.hero-mid{
    width: 100%;
    height: 85vh;
    position: relative;
    
}

.hero-mid h1{
   
    padding: 4rem !important;
    
}
img{
    width: 100%;
    height: 100%;
    object-fit: cover;
}
.hero-text{
    position: absolute;
    top: 55%;
    left: 55%;
    transform: translate(-50%, -50%);
  
}

 .hero-text h1{
    font-size: 3rem;
    font-weight: 800;
    background: #000 ;
    -webkit-background-clip: text; 
    -webkit-text-fill-color: transparent;
}

.hero-text p{

    font-size: 1.3rem;
    color: white;
    padding: 0.5rem 0 2rem 0;
} 
.hero-text .show{
    text-decoration: none;
    background:#fff;
    padding: 1rem 2rem;
    border-radius: 6px;
    font-size: 1.1rem;
    font-weight: bold;
    letter-spacing: 2px;
    color: #222;
}
.hero-text:hover a{
    background: black;
    color: white;
}
.hero-text .hide{
    display: none;
}
@media screen and(max-width:555px){
    .hero-text h1{
        padding: 10px 20px;
    }
    .hero-text p{
        font-size: 1.1rem;
        padding: 0 0 2rem 0;
    }
    .hero-text .show{
       
        padding: .6rem 1.1rem;
        border-radius: 6px;
        font-size: 1rem;
       background: red;
    }
    
}